










































import AboutHero from '@/components/AboutHero.vue';
import VerticalLogo from '@/components/VerticalLogo.vue';
import ProjectCarousel from '@/components/ProjectCarousel.vue';
import TeamComponent from '@/components/TeamComponent.vue';
import ServiceComponent from '@/components/ServiceComponent.vue';
import FooterComponent from '@/components/FooterComponent.vue';

export default {
  name: 'About',
  components: {
    'about-hero': AboutHero,
    'vertical-logo': VerticalLogo,
    projects: ProjectCarousel,
    teams: TeamComponent,
    services: ServiceComponent,
    'vue-footer': FooterComponent,
  },
  beforeCreate: () => {
    const navCheck = document.querySelector('#nav-check') as HTMLFormElement;
    if (navCheck.checked === true) {
      navCheck.checked = false;
    }
  },
};

